import { IResourceDictionary, filters } from "pixi.js"
import {
  AnimationBase,
  AnimationHelper
} from "../../engine"

/**
 *
 */
export class Door extends AnimationBase {
  constructor(
    resources: IResourceDictionary,
    done?: () => void
  ) {
    super(resources)

    const which = "things.door"

    this._clips = {
      [which]: AnimationHelper.loadClip(
        this._resources,
        which,
        0.01,
        8
      )
    }

    this._clips[which].filters = [
      new filters.BlurFilter(0.2)
    ]

    this._playClip(which, false, false).then(() => {
      if (done) {
        done()
      }
    })
  }
}
