import {
  AnimationBase,
  AnimationHelper
} from "../../../engine"
import { IResourceDictionary } from "pixi.js"

/**
 *
 */
export class Blood extends AnimationBase {
  constructor(
    resources: IResourceDictionary,
    speed: number = 0.01
  ) {
    super(resources)
    const which = "things.blood"

    this._clips = {
      [which]: AnimationHelper.loadClip(
        this._resources,
        which,
        speed,
        1
      )
    }

    this._playClip(which, false, false)
  }
}
